.footer {
    max-width: 880px;
    align-items: center;
    margin: 0 auto 60px;
}

@media screen and (max-width: 917px) {
    .footer {
        max-width: 581px;
    }
}

@media screen and (max-width: 610px) {
    .footer {
        max-width: 282px;
        margin: 0 auto 36px;
    }
}